* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

body {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: left;
          align-items: left;
  min-height: 100vh;
  color: @df-off-black;
  font-family: @df-serif;
  font-size: @df-text-base;
  line-height: @df-line-base;
  padding: 0;
  margin: 0;
  touch-action: manipulation;
  background-color: rgb(192, 192, 192);
}

h1 {
  font-size: 28px;
  line-height: 30 / 28;
  letter-spacing: -0.5px;
}

.source {
  margin: 6px auto 0;
  max-width: 960px;
  line-height: 1.2;
  font-size: 14px;
  font-style: italic;
  font-family: Helvetica Neue;
  color: @df-middle-gray;
}

.full-screen-square {
  position: fixed;
  left: 0;
  width: 100%;
  cursor: pointer;
  text-align: center;
  background-color: rgb(192, 192, 192);
  height: 100%;
  padding-top: 20px;
}

.full-screen-square h2 {
padding-top: 10px;
  font-size: 22px;
  color: #000000;
  text-align: left;
  margin: 0 50px;
  font-family: Helvetica Neue;
  text-transform: uppercase;
}

.full-screen-square h3 {
  font-size: 16px;
  color: rgb(188, 92, 35);
  text-align: left;
  margin-top: 5px;
  margin-left: 50px;
  font-family: Helvetica Neue;
  font-style: italic;
}

.full-screen-square h4 {
  font-size: 16px;
  letter-spacing: -0.5px;
  color: rgb(188, 92, 35);
  text-align: left;
  margin-top: -10px;
  margin-left: 50px;
  font-family: Helvetica Neue;
  font-style: normal;
}

.full-screen-square h5 {
  font-size: 15px;
  letter-spacing: -0.5px;
  color: #000000;
  text-align: left;
  margin: 0 50px;
  font-family: Helvetica Neue;
}

.full-screen-square img {
  width: 100%;
  object-fit: contain;
  background-color: rgb(192, 192, 192);
  overflow: scroll;
  max-width: 900px;
}
.square {
  max-width: 310px;
  cursor: pointer;
  -webkit-justify-content: center;
          justify-content: center;
}

.square image {
  max-width: 310px;
  cursor: pointer;
  -webkit-justify-content: center;
          justify-content: center;
}

.list {
  display: -webkit-flex;
  display: flex;
  list-style-type: none;
  -webkit-flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

.image {
  top: 0px;
  left: 0;
  width: 100%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding-bottom: 5px;
}

.img {
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding-bottom: 5px;
}

button {
  border-radius: 8px;
  height: 40px;
  line-height: 1.2;
  font-size: 14px;
  font-family: @df-sans;
  margin-left: 10px;
  margin-bottom: 10px;
  -webkit-justify-content: center;
          justify-content: center;
}

.outmost {
  max-width: 960px;
  width: 100%;
  position: relative;
  margin: 20px 16px;
}

.groupButton {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
